import { DragHandle } from '@material-ui/icons'
import React, { useState,useEffect, useRef } from 'react'

//logo
import logo from "../assets/logo.svg"
import scrolledLogo from "../assets/logo-mobile.svg"
import { Link } from 'react-router-dom'

function Header({showSideMenHandler, className}) {

  const headerRef=useRef()
  const [isScrolledHeader,setIsScrolledHeader]=useState(false)

  useEffect(_=>{
    window.onscroll=()=>{
      if(window.pageYOffset>100){
        headerRef.current.classList.add("header-scrolled")
        setIsScrolledHeader(true)
      }
      if(window.pageYOffset==0){
        headerRef.current.classList.remove(!className&&"header-scrolled")
        setIsScrolledHeader(false)
      }
    }
  },[])
  return (
    <header ref={headerRef} className={"header "+className}>
        <div className="header__logo">
            {/* <img src={isScrolledHeader?scrolledLogo:className?scrolledLogo:logo}/> */}
            <img src="https://digitalfriendly.agency/wp-content/uploads/2021/10/logo-DF.png?fbclid=IwAR07YO152XGm2q8vrztB-8vF4bsAwNOMJzsE_BmdCd8MPWS_zFmjrbMTbyY" style={{width:"8rem"}}/>
        </div>
        <ul className="header__list">
            <li className="header__list__item"><Link to="/">Accueil</Link>  </li>
            <li className="header__list__item"><a href="#marque">Marque</a>  </li>
            <li className="header__list__item"><Link to="/test_drive">Test Drive</Link>  </li>
            <li className="header__list__item"><Link to="/test_drive">Showroom</Link>  </li>
            <li className="header__list__item"><Link to="/contact">Contact</Link>  </li>
        </ul>
        <DragHandle onClick={showSideMenHandler} className="header__menu icon icon--menu" />
    </header>
  )
}

export default Header