import {
  AccountBalance,
  AttachMoney,
  Build,
  Call,
  DirectionsCar,
  Search,
  Settings,
} from "@material-ui/icons";
import React, { useState, useRef, useEffect } from "react";
import Header from "../components/Header";

// markes logo
import mahindra from "../assets/mahindra.png";
import geely from "../assets/geely.jpg";
import ssangyoung from "../assets/ssangyoung.png";
import SingleCarCard from "../components/SingleCarCard";
import CarOptionCard from "../components/CarOptionCard";

// car option card logo
import one from "../assets/1.svg";
import two from "../assets/2.png";
import three from "../assets/3.svg";
import four from "../assets/4.svg";
import Footer from "../components/Footer";
import SideMenu from "../components/SideMenu";

//gsap animation
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

//framer motion animation
//frame motion
import { motion } from "framer-motion";

//carList info
import carsInfo from "../data/carsInfo";
import { useNavigate } from "react-router-dom";

const homeContainer = {
  exit: {
    y: "-100vh",
    opacity: 0,
    transition: { duration: 2 },
  },
  hidden: {
    y: "-100vh",
    opacity: 0,
  },
  shown: {
    y: 0,
    opacity: 1,
    transition: { type: "tween" },
  },
};

gsap.registerPlugin(ScrollTrigger);

const Main = () => {
  console.log(gsap);
  const [modelFilterIndex, setModelFilterIndex] = useState(0);
  const [slideMenuIsShown, setSideMenuIsShown] = useState(false);

  const models = ["Geely", "Mahindra", "Ssangyong"];
  const [modelValue, setModalValue] = useState("Geely");
  const changeModelFilterIndex = (index, modelName) => {
    setModelFilterIndex(index);
    setModalValue(modelName);
  };

  const filtredCarsList = carsInfo.filter(
    (ele) => ele.marke.trim().toLowerCase() == modelValue.trim().toLowerCase()
  );
  console.log(filtredCarsList, "###########");
  // first section animation ref
  const oneTitleRef = useRef();
  const oneDescRef = useRef();
  const oneFilter = useRef();

  // second section animation ref
  const twoRef = useRef();

    useEffect(_=>{
      window.scrollTo({ top: 0, behavior: "auto" });
    //section one
      const tl=gsap.timeline();
      tl.from(oneTitleRef.current,{
        opacity:0,
        y:100
      })
      .from(oneDescRef.current,{
        opacity:0,
        y:200
      })
      .from(oneFilter.current,{
        opacity:0,
        scale:0.7,
        y:100,
        duration:.5
      })
  //section two
      gsap.from(twoRef.current,{
        opacity:0,
        y:200,
        scrollTrigger:{
          trigger:".section__two",
          start:"top 70%",
          end:"bottom 90%",
          scrub:true
        }
      })
  // section three
  gsap.from(".section__three>img",{
    opacity:0,
    scrollTrigger:{
      trigger:".section__three",
      start:"top 70%",
      end:"bottom 80%",
      scrub:true,
    }

  })
  //section four
      gsap.from(".section__four__cars > *",{
        opacity:0,
        x:-300,
        scrollTrigger:{
          trigger:".section__four",
          start:"top 70%",
          end:"bottom 90%",
          scrub:true
        },
        stagger: 1
      })

  //section five
  gsap.from(".section__five__left h1",{
    scale:0,
    scrollTrigger:{
      trigger:".section__five",
      top:"top 70%",
      end:"top 60%",
      scrub:true
    }
  })

  //section sex
  gsap.from(".section__sex__offers__item",{
    scale:0,
    scrollTrigger:{
      trigger:".section__sex",
      top:"top 70%",
      end:"bottom 85%",
      scrub:true
    }
  })

    },[])

  const sideMenuItemAnimationHandler = () => {
    setSideMenuIsShown(true);
    const tl = gsap.timeline();
    tl.fromTo(
      ".side-menu__list__item",
      {
        x: -100,
        opacity: 0,
        stagger: 0.3,
      },
      {
        x: 0,
        opacity: 1,
        stagger: 0.3,
      }
    );
  };
  const [marke, setMarke]=useState("geely")
  const [searchMarke,setSearchMarke]=useState("")
  const [searchModel,setSearchModel]=useState(null)
  const navigate=useNavigate()
  
  return (
    <motion.div
    //  variants={homeContainer}
    //   exit="exit"
    //   initial="hidden"
    //   animate="shown"
    >
      <SideMenu
        closeSideMenuHandler={setSideMenuIsShown}
        isShown={slideMenuIsShown}
      />
      <Header
        className={false}
        showSideMenHandler={sideMenuItemAnimationHandler}
      />
      <section className="section section__one">
        <div className="section__one__center">
          <span ref={oneTitleRef}>AUTOFEST</span>
          <span ref={oneDescRef}>Festival de l'automobile</span>
          <div ref={oneFilter} className="section__one__filter">
          <div className="section__one__filter__model">
              <span>Modle</span>
              <select   onChange={(e)=>{
                setMarke(e.target.value.toLocaleLowerCase())
                setSearchMarke(e.target.value)
              }}>
                <option value="Geely">Geely</option>
                <option value="Mahindra">Mahindra</option>
                <option value="Ssangyong">Ssangyong</option>
              </select>
            </div>
            <div className="section__one__filter__marke">
              <span>Marque</span>
              <select
              onChange={(e)=>{
                setSearchModel(e.target.value)
              }}
              >
                {marke == "Geely".toLowerCase() && (
                  <>
                    <option value="Selecter une modle ">
                      Selecter une modle
                    </option>
                    <option value="Coolray">Coolray</option>
                  </>
                )}
                {marke == "Mahindra".toLowerCase() && (
                  <>
                    <option value="Selecter une modle">
                      Selecter une modle
                    </option>
                    <option value="XUV_300">XUV_300</option>
                    <option value="KUV_100">KUV_100 </option>
                  </>
                )}
                {marke == "Ssangyong".toLowerCase() && (
                  <>
                    <option value="Selecter une modle">
                      Selecter une modle
                    </option>
                    <option value="REXTON">REXTON </option>
                    <option value="MUSSO">MUSSO </option>
                    <option value="KORANDO">KORANDO</option>
                    <option value="TIVOLI">TIVOLI</option>
                  </>
                )}
              </select>
            </div>
            
            <div className="section__one__filter__search-btn">
              <div onClick={()=>{
                if(searchModel){
                  navigate(`voiture/${searchModel}`)
                }
              }} >
                <Search className="icon icon--search" />
                <span>Chercher</span>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section ref={twoRef} className="section__two">
        <div className="section__two__title">AUTOFEST : cest quoi?</div>
        <div className="section__two__desc">
          Lorem ipsum dolor sit amet consectetur adipisicing elit. Laudantium
          minima corrupti recusandae numquam molestias, tempora facere quisquam
          accusamus aliquid nam cupiditate fugit? Necessitatibus vero voluptas
          nisi consequatur animi quos earum! Necessitatibus aliquid beatae
          minima aut ad incidunt explicabo illo accusantium.
        </div>
      </section>
      <section className="section__three">
        <img src={ssangyoung} alt="ssangyoung" />
        <img src={geely} alt="geely" />
        <img src={mahindra} alt="mahindra" />
      </section>
      <section className="section__four" id="marque">
        {/* <div className="section__four__title">
            Liste Des Modles
        </div> */}
        <div className="section__four__filter">
          {models.map((ele, index) => {
            return (
              <div
                className={index == modelFilterIndex && "active"}
                onClick={changeModelFilterIndex.bind(null, index, ele)}
              >
                {ele}
              </div>
            );
          })}
        </div>
        <ul className="section__four__cars">
          {filtredCarsList.map((ele, index) => {
            return (
              <SingleCarCard
                key={index}
                photo={ele.photo[0]}
                title={ele.name}
                price={ele.price}
              />
            );
          })}
        </ul>
      </section>
      <section className="section__five">
        <div className="section__five__left">
          <h1>Facilits de financement automatique</h1>
          <div>
            Lorem ipsum dolor sit amet consectetur adipisicing elit. Vitae
            veritatis, atque qui doloribus ipsum ratione consequatur delectus
            eaque accusamus eligendi sequi, modi, incidunt mollitia! Ut ipsam
            voluptates rem aperiam ipsum!
          </div>
          {/* <h2>Rservez votre inspection gratuite</h2> */}
          <div>
            Lorem ipsum dolor sit amet consectetur adipisicing elit. Ab qui non,
            accusantium corrupti ullam incidunt.
          </div>
          <div className="section__five__left__contact">
            <Call />
            <span>Appelez-nous pour rserver: </span>
            <span>+216 000 000 00</span>
          </div>
        </div>
        <div className="section__five__right__wrapper">
          <div
            className="section__five__right"
            style={{ transform: "translateY(10rem)" }}
          >
            <CarOptionCard content="Lorem ipsum dolor sit." png={one} />
            <CarOptionCard content="Lorem ipsum dolor sit." png={two} />
          </div>
          <div className="section__five__right">
            <CarOptionCard content="Lorem ipsum dolor sit." png={three} />
            <CarOptionCard content="Lorem ipsum dolor sit." png={four} />
          </div>
        </div>
      </section>
      <section className="section__sex">
        <div
          className="section__sex__title"
          style={{ textTransform: "capitalize" }}
        >
          Ce quon offre
        </div>
        <ul className="section__sex__offers">
          <li className="section__sex__offers__item">
            <div className="section__sex__offers__item__img">
              <AttachMoney className="icon icon--offers" />
            </div>
            <div className="section__sex__offers__item__title">
              Des prix exceptionnels
            </div>
            <div className="section__sex__offers__item__content">
              <span>
                Lorem ipsum dolor sit amet consectetur, adipisicing elit. Nulla,
                dignissimos necessitatibus. Vel tempora possimus velit?
              </span>
              <div
                className="section__sex__offers__item__content__bg"
                style={{
                  backgroundImage:
                    "url('https://images.moneycontrol.com/static-mcnews/2021/03/Car-price-rising.jpg?impolicy=website&width=770&height=431')",
                }}
              ></div>
            </div>
          </li>
          <li className="section__sex__offers__item">
            <div className="section__sex__offers__item__img">
              <AccountBalance className="icon icon--offers" />
            </div>
            <div className="section__sex__offers__item__title">
              Financement leasing
            </div>
            <div className="section__sex__offers__item__content">
              <span>
                Lorem ipsum dolor sit amet consectetur, adipisicing elit. Nulla,
                dignissimos necessitatibus. Vel tempora possimus velit?
              </span>
              <div
                className="section__sex__offers__item__content__bg"
                style={{
                  backgroundImage:
                    "url('https://www.dynamique-mag.com/wp-content/uploads/leasing.jpeg')",
                }}
              ></div>
            </div>
          </li>
          <li className="section__sex__offers__item">
            <div className="section__sex__offers__item__img">
              <DirectionsCar className="icon icon--offers" />
            </div>
            <div className="section__sex__offers__item__title">Test Drive</div>
            <div className="section__sex__offers__item__content">
              <span>
                Lorem ipsum dolor sit amet consectetur, adipisicing elit. Nulla,
                dignissimos necessitatibus. Vel tempora possimus velit?
              </span>
              <div
                className="section__sex__offers__item__content__bg"
                style={{
                  backgroundImage:
                    "url('https://autlines.tm-colors.info/wp-content/uploads/2019/10/offers03.jpg')",
                }}
              ></div>
            </div>
          </li>
        </ul>
      </section>
      <div className="section__seven">
        <div className="section__seven__title">Partenaires</div>
        <div className="section__seven__top">
          <img src="https://www.stbfinance.com.tn/sites/default/files/2022-03/attijarileasing-attijari-leasing.png" />
          <img src="https://www.keejob.com/media/recruiter/recruiter_13640/logo-13640-20170912-104357.jpg" />
        </div>
        <div className="section__seven__bottom">
          <img src="https://www.1min30.com/wp-content/uploads/2017/06/Shell-logo.jpg" />
          <img src="https://logos-world.net/wp-content/uploads/2020/08/Bosch-Logo-1981-2002.png" />
          <img src="https://upload.wikimedia.org/wikipedia/commons/thumb/2/2b/Valeo_Logo.svg/2560px-Valeo_Logo.svg.png" />
        </div>
      </div>
      <Footer />
    </motion.div>
  );
};

export default Main;
