import React, { useState, useRef, useEffect } from "react";
import Footer from "../components/Footer";
import Header from "../components/Header";
import SideMenu from "../components/SideMenu";

//gsap animation
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";


//framer motion animation
import { motion } from "framer-motion";
import { Done, Email } from "@material-ui/icons";

gsap.registerPlugin(ScrollTrigger);

const contactContainer = {
  exit: {
    x: "-100vw",
    opacity: 0,
    transition: { duration: 2 },
  },
  hidden: {
    x: "-100vw",
  },
  shown: {
    x: 0,
    transition: { type: "tween" },
  },
};

const Contact = () => {
  const [slideMenuIsShown, setSideMenuIsShown] = useState(false);

  const sideMenuItemAnimationHandler = () => {
    setSideMenuIsShown(true);
    const tl = gsap.timeline();
    tl.fromTo(
      ".side-menu__list__item",
      {
        x: -100,
        opacity: 0,
        stagger: 0.3,
      },
      {
        x: 0,
        opacity: 1,
        stagger: 0.3,
      }
    );
  };

 
  // AUTO TOP SCROLL
  const rightRef = useRef();
  useEffect((_) => {
    window.scrollTo({ top: 0, behavior: "auto" });
  }, []);

  return (
    <motion.div
      variants={contactContainer}
      exit="exit"
      initial="hidden"
      animate="shown"
    >
      <SideMenu
        closeSideMenuHandler={setSideMenuIsShown}
        isShown={slideMenuIsShown}
      />
      <Header
        className="header-scrolled"
        showSideMenHandler={sideMenuItemAnimationHandler}
      />
      <div className="single-car-page contact">
        <div className="single-car-page__title contact__title">
          <span>Contactez-Nous </span>
          <motion.div 
            initial={{y:-10,scale:.6}}
            animate={{y:0,scale:1.3,duration:4}}
            transition={{duration:1000,type:"spring",stiffness:1000}}
          >
              <Email className="icon icon--contact" />
          </motion.div>
        </div>
        <div className="single-car-page__flex contact__flex " style={{justifyContent:"flex-start"}}>

          <div className="single-car-page__flex__left contact__left">
                <div className="contact__left__title">
                Adresse
                </div>
                <div className="contact__left__desc">
                    Lorem ipsum dolor sit amet consectetur adipisicing elit. Fuga, praesentium?
                </div>
                <div className="contact__left__tlf">+216 000 00 00</div>
                <div className="contact__left__email">Email@email.com</div>
          </div>

          <div ref={rightRef} className="single-car-page__flex__right contact__right">
            <div className="contact__right__title">
            Formulaire de contact
            </div>
            <div className="contact__right__form">
                <div className="contact__right__form__input-wrapper">
                    <input placeholder="*nom" type="text" className="contact__right__form__input" />
                    <input placeholder="*prenom" type="text" className="contact__right__form__input" />
                </div>
                <div className="contact__right__form__input-wrapper">
                    <input placeholder="email" type="email" className="contact__right__form__input" />
                    <input placeholder="Numro de tlphone" type="text" className="contact__right__form__input" />
                </div>
                <textarea className="contact__right__form__textarea" placeholder="Message ... "/>
                <button className="contact__right__form__btn">
                    Envoyer le message
                </button>
            </div>
          </div>

        </div>
      </div>
      <Footer />
    </motion.div>
  );
};

export default Contact;
