import React, { useState, useRef, useEffect } from "react";
import ReactDOM from "react-dom";
import Footer from "../components/Footer";
import Header from "../components/Header";
import SideMenu from "../components/SideMenu";

//gsap animation
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";


//framer motion animation
import { motion } from "framer-motion";
import { Done } from "@material-ui/icons";

//react router dom 
import { useParams } from "react-router-dom";
import TestDrivePopUp from "./TestDrivePopUp";

gsap.registerPlugin(ScrollTrigger);

const contactContainer = {
  exit: {
    x: "-100vw",
    opacity: 0,
    transition: { duration: 2 },
  },
  hidden: {
    x: "-100vw",
  },
  shown: {
    x: 0,
    transition: { type: "tween" },
  },
};

const TestDrive = () => {
  const [slideMenuIsShown, setSideMenuIsShown] = useState(false);
  const [showPopUp, setShowPopUp]=useState(false)

  const sideMenuItemAnimationHandler = () => {
    setSideMenuIsShown(true);
    const tl = gsap.timeline();
    tl.fromTo(
      ".side-menu__list__item",
      {
        x: -100,
        opacity: 0,
        stagger: 0.3,
      },
      {
        x: 0,
        opacity: 1,
        stagger: 0.3,
      }
    );
  };

  const [marke, setMarke]=useState("geely")

  // calendar fetch date
  const daysNameArray=["Lun","Mar","Mer","Jeu","Ven","Sam","Dim"]
  const daysOne=[1,2,3,4,5,6,7]
  const daysTwo=[8,9,10,11,12,13,14]
  const daysThree=[15,16,17,18,19,20,21]
  const daysFour=[22,23,24,25,26,27,26]
  const [dateIndice, setDateIndice]=useState(null)

  const calendarIndiceHandler=()=>{
    if(name.toLowerCase()=="Coolray".toLocaleLowerCase()){
      setDateIndice(8)
      console.log("here")
  }
  else if(name.toLowerCase()=="XUV 300".toLocaleLowerCase()){
      setDateIndice(10)
  }
  else if(name.toLowerCase()=="TIVOLI".toLocaleLowerCase()){
      setDateIndice(27)
  }
  else{
    setDateIndice(null)
  }
  }

  const modelShowDateCalendarHandler=(e)=>{
    console.log(e.target.value)
    if(e.target.value.trim()=="Coolray"){
      setDateIndice(8)
      console.log("here")
  }
  else if(e.target.value=="XUV 300"){
      setDateIndice(10)
  }
  else if(e.target.value=="TIVOLI"){
      setDateIndice(27)
  }
  else{
    setDateIndice(null)
  }
  }

  const {marque, name}=useParams()

  // auto scroll top
  const rightRef = useRef();
  const marqueRef=useRef()
  const nameRef=useRef()
  useEffect((_) => {
  window.scrollTo({ top: 0, behavior: "auto" });
  if(name&&marque){
    marqueRef.current.value=marque.trim()
    setMarke(marque.toLowerCase())
      if(name.includes("_")){
        const newName=name.split("_").join(" ").toLocaleLowerCase()
        setTimeout(_=>nameRef.current.value=newName,100)  
      
      }
      setTimeout(_=>{
        nameRef.current.value=name;
        calendarIndiceHandler()
      },100)  
  }
  
  }, []);

  return (
    <motion.div
      variants={contactContainer}
      exit="exit"
      initial="hidden"
      animate="shown"
    >
      {showPopUp&&ReactDOM.createPortal(<TestDrivePopUp onClose={setShowPopUp.bind(null, false)} />,document.getElementById("popUps"))}
      <SideMenu
        closeSideMenuHandler={setSideMenuIsShown}
        isShown={slideMenuIsShown}
      />
      <Header
        className="header-scrolled"
        showSideMenHandler={sideMenuItemAnimationHandler}
      />
      <div className="single-car-page">
        <div className="single-car-page__title">
          <span>Test Drive :</span>
        </div>
        <div className="single-car-page__flex" style={{justifyContent:"flex-start"}}>
          <div className="single-car-page__flex__left calendar__left">
            <div className="test-drive__form">
              <span>Marke</span>
              <select ref={marqueRef} className="test-drive__form__marke" onChange={(e)=>setMarke(e.target.value.toLocaleLowerCase())}>
                <option value="Geely">Geely</option>
                <option value="Mahindra">Mahindra</option>
                <option value="Ssangyong">Ssangyong</option>
              </select>

              <span>Modle</span>
              <select ref={nameRef} className="test-drive__form__model" onChange={modelShowDateCalendarHandler}>
                {marke=="Geely".toLowerCase()&&
                  <>
                    <option value="Selecter une modle ">Selecter une modle </option>
                    <option value="Coolray">Coolray</option>
                  </>
                }
                {marke=="Mahindra".toLowerCase()&&
                  <>
                    <option value="Selecter une modle">Selecter une modle</option>
                    <option value="XUV 300">XUV 300</option>
                    <option value="KUV 100">KUV 100 </option>
                  </>
                }
                {marke=="Ssangyong".toLowerCase()&&
                  <>
                    <option value="Selecter une modle">Selecter une modle</option>
                    <option value="REXTON">REXTON </option>
                    <option value="MUSSO">MUSSO </option>
                    <option value="KORANDO">KORANDO</option>
                    <option value="TIVOLI">TIVOLI</option>
                  </>
                }
              </select>

              <span>Date :</span>
              <input type="date" className="test-drive__form__date"/>
                
                <div onClick={setShowPopUp.bind(null, true)} className="test-drive__form__btn">
                    <span>Rserver</span>
                    <Done />
                </div>

            </div>

          </div>

          <div ref={rightRef} className="single-car-page__flex__right calendar__right">
            <div className="calendar__right__title">
            Fvrier 2023
            </div>
            <div className="calendar calendar__one">
                {daysNameArray.map((ele)=><div className="inner">{ele}</div>)}
            </div>

            <div className="calendar calendar__two">
                {daysOne.map(ele=><div className={`inner ${+ele==dateIndice&&"active"}`}>{ele}</div>)}
            </div>
            <div className="calendar calendar__two">
                {daysTwo.map(ele=><div className={`inner ${+ele==dateIndice&&"active"}`}>{ele}</div>)}
            </div>
            <div className="calendar calendar__two">
                {daysThree.map(ele=><div className={`inner ${+ele==dateIndice&&"active"}`}>{ele}</div>)}
            </div>
            <div className="calendar calendar__two">
                {daysFour.map(ele=><div className={`inner ${+ele==dateIndice&&"active"}`}>{ele}</div>)}
            </div>
            

          </div>
        </div>
      </div>
      <Footer />
    </motion.div>
  );
};

export default TestDrive;
