import { Close } from '@material-ui/icons'
import React from 'react'
import { Link } from 'react-router-dom'


//logo
import logo from "../assets/logo.svg"


const SideMenu = ({isShown, closeSideMenuHandler}) => {

  return (
    <div className="side-menu" style={{left:isShown?"0":"-70vw"}}>
        <Close onClick={closeSideMenuHandler.bind(null,false)} className="icon icon--close" />
        <div className="side-menu__logo">
            {/* <img src={logo}/> */}
          <img src="https://digitalfriendly.agency/wp-content/uploads/2021/10/logo-DF.png?fbclid=IwAR07YO152XGm2q8vrztB-8vF4bsAwNOMJzsE_BmdCd8MPWS_zFmjrbMTbyY" style={{width:"8rem"}}/>
        </div>
        <ul className="side-menu__list">
            <li className="side-menu__list__item"><Link to="/">Accueil</Link>  </li>
            <li className="side-menu__list__item"><a href="/contact">Marque</a>  </li>
            <li className="side-menu__list__item"><Link to="/test_drive">Test Drive</Link>  </li>
            <li className="side-menu__list__item"><Link to="/">Showroom</Link>  </li>
            <li className="side-menu__list__item"><Link to="/contact">Contact</Link>  </li>
        </ul>
    </div>
  )
}

export default SideMenu