import { DriveEta, LocalGasStation, RemoveRedEye, Star } from '@material-ui/icons'
import React from 'react'
import { Link } from 'react-router-dom'

//framer motion
import {motion} from "framer-motion"

const SingleCarCard = ({photo, title, price}) => {
  return (
    <motion.li className="car-card"
      exit={{scale:.5, opacity:0}}
      transition={{duration:1000,type:"spring",stiffness:1000}}
    >
        <div className="car-card__img">
            <img src={photo||"http://demoapus1.com/voiture/wp-content/uploads/2021/12/listing22-621x396.jpg"} />
            <div className="car-card__img__bg">
                <a href={"/voiture/"+title}>
                  <RemoveRedEye className="icon icon--see" />
                </a>
            </div>
        </div>
        <div className="car-card__price">TND {price||"18.200"}</div>
        <div className="car-card__title">{title||"Chevrolet Colorado  2021"}</div>
        <hr />
        <div className="car-card__info">
            <span>
                    <LocalGasStation />
                    <span>Essence</span>
            </span>
            <span>
                    <DriveEta />
                    <span>BVA</span>
            </span>
        </div>
    </motion.li>
  )
}

export default SingleCarCard