import { Close } from '@material-ui/icons'
import React from 'react'

const TestDrivePopUp = ({onClose}) => {
  return (
    <div className="testDrivePopUp">
        <div className="testDrivePopUp__exit" onClick={onClose}></div>
        <div className="testDrivePopUp__form">
            <Close className="icon icon--close" onClick={onClose} />
            <div className="testDrivePopUp__form__title">
                Complter votre information
            </div>
            <input placeholder="Nom et prnom" type="text" className="testDrivePopUp__form__input" />
            <input placeholder="Email" type="email" className="testDrivePopUp__form__input" />
            <textarea placeholder="Message ..." type="text" className="testDrivePopUp__form__textarea" />
            <button className="testDrivePopUp__form__btn">Reserver</button>
        </div>
    </div>
  )
}

export default TestDrivePopUp