import React, { useState, useRef ,useEffect } from 'react'
import Footer from '../components/Footer'
import Header from '../components/Header'
import SideMenu from '../components/SideMenu'


//gsap animation
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import { ArrowBackIosOutlined, ArrowForwardIosOutlined, Call, SportsMotorsports } from '@material-ui/icons';
import SingleCarCard from '../components/SingleCarCard';


//framer motion animation
import {motion} from "framer-motion"

//react router dom
import { Link, useParams } from 'react-router-dom';
import carsInfo from '../data/carsInfo';

gsap.registerPlugin(ScrollTrigger);

const contactContainer={
    exit:{
        x:"-100vw",
        opacity:0,
        transition:{duration:2}
    },
    hidden:{
        x:"-100vw",
    },
    shown:{
        x:0,
        transition:{type:"tween"}
    }
}




const SingleCarPage = () => {

  const {id,idTwo}=useParams()

  const selectedCarInfo=carsInfo.find(ele=>ele.name.trim().toLowerCase()==id.trim().toLocaleLowerCase())
  const similarCarsInfo=carsInfo.filter(ele=>ele.marke===selectedCarInfo.marke&&ele.name!==selectedCarInfo.name)

  const [slideMenuIsShown, setSideMenuIsShown]=useState(false)

  const [showDevis, setShowDevis]=useState(false)

  const sideMenuItemAnimationHandler=()=>{
    setSideMenuIsShown(true)
    const tl=gsap.timeline();
      tl.fromTo(".side-menu__list__item",{
        x:-100,
        opacity:0,
        stagger:0.3
      },
      {
        x:0,
        opacity:1,
        stagger:0.3
      })    
  }

  const imgRef=useRef()
  const sliderRef=useRef()


  const scrollSliderHandler=(direction)=>{
    const scrollWidth=imgRef.current.clientWidth;
    console.log(imgRef.current)
    sliderRef.current.scrollBy({
      top:0,
      left:direction=="next"?scrollWidth:-scrollWidth,
      behavior:"smooth"
    })
    if(opacityCounter==0){
      if(direction=="back"){
        return
      }
      if(direction=="next")setOpacityCounter(prev=>prev+1)

    }
    else if(opacityCounter!=0&&opacityCounter<images.length-1){
      console.log("ok",opacityCounter)
      if(direction=="next")setOpacityCounter(prev=>prev+1)
      if(direction=="back")setOpacityCounter(prev=>prev-1)
    }
    else if(opacityCounter!=0&&opacityCounter==images.length-1){
      if(direction=="next"){
        return
      }
      if(direction=="back")setOpacityCounter(prev=>prev-1)

    }
  }

  const images=[1,2,3,4]
  const carDetailsColumns=[1,2,3,4,5,6,7,8,9]
  const [opacityCounter, setOpacityCounter]=useState(0)
  
  const smallSliderImageClickHandler=(index)=>{
    setOpacityCounter(index)
    sliderRef.current.scrollTo({
      top:0,
      left:index*imgRef.current.clientWidth,
      behavior:"smooth"
    })
  }

  // animation
  const rightRef=useRef()
useEffect(_=>{
  window.scrollTo({ top: 0, behavior: "auto" });
  if(rightRef.current){
    gsap.fromTo(rightRef.current,{
      x:1000,
      opacity:0,
      duration:2
    },{
      x:0,
      opacity:1
    })
  }
},[])

  return (
    <motion.div
      variants={contactContainer}
      exit="exit"
      initial="hidden"
      animate="shown"
    >
      <SideMenu closeSideMenuHandler={setSideMenuIsShown} isShown={slideMenuIsShown} />
      <Header className="header-scrolled" showSideMenHandler={sideMenuItemAnimationHandler} />
      <div className="single-car-page">
        <div className="single-car-page__title">
          <span>{selectedCarInfo.marke} / {selectedCarInfo.name}</span>
          <span>TND {selectedCarInfo.price}</span>
        </div>
        <div className="single-car-page__flex">

          <div className="single-car-page__flex__left">
            <div className="single-car-page__flex__left__wrapper" style={{position:"relative"}}>
                <div ref={sliderRef} className="single-car-page__flex__left__slider">
                  <ArrowBackIosOutlined onClick={scrollSliderHandler.bind(null,"back")} className="icon icon--slider-back"/>
                  <ArrowForwardIosOutlined onClick={scrollSliderHandler.bind(null,"next")} className="icon icon--slider-next"/>
                  {selectedCarInfo.photo.map((img,index)=>{
                    return <img onClick={e=>console.log(e.target.clientWidth)}  ref={imgRef} src={img} key={index} />
                  })}
                  
                </div>
            </div>
            <div className="single-car-page__flex__left__small-imgs">

                  {selectedCarInfo.photo.map((img,index)=>{
                    return <img src={img} key={index} onClick={smallSliderImageClickHandler.bind(null,index)} style={{opacity:opacityCounter==index&&0.5}}  />
                  })}
            </div>
            <div className="single-car-page__flex__left__car-details">
              <span>Fiche technique</span>
              {carDetailsColumns.map((ele,index)=>{
                return <React.Fragment key={index}>
                <div  className="single-car-page__flex__left__car-details__columns">
                    <span>Taper:</span>
                    <span>Sedan</span>
                </div>
                <hr />
                </React.Fragment>

              })}
            </div>

          </div>


          <div ref={rightRef} className="single-car-page__flex__right">
          <Link to={"/test__drive/"+selectedCarInfo.marke+"/"+id} className="single-car-page__flex__right__btn-blue">
            <SportsMotorsports  className="icon icon--drive"/>
            <span>Programmer un test drive</span>
          </Link>
          <button onClick={setShowDevis.bind(null, true)} className="single-car-page__flex__right__btn-yellow">Demander un devis</button>

              {showDevis&&<div className="single-car-page__flex__right__contact">
                <div className="single-car-page__flex__right__contact__owner">
                  <img src="http://demoapus1.com/voiture/wp-content/uploads/wp-cardealer-uploads/_listing_gallery/2021/12/13-150x150.jpg"  />
                  <div>
                      <span>administrateur</span>
                      <span>
                        <Call icon="icon icon--call"/>
                        <span>+216 00 000 00</span>
                      </span>
                  </div>
                </div>
                <input placeholder="Nom complet" type="text" className="single-car-page__flex__right__contact__input" />
                <input placeholder="Email" type="text" className="single-car-page__flex__right__contact__input" />
                <input placeholder="Telehone" type="text" className="single-car-page__flex__right__contact__input" />
                <textarea placeholder="Message" type="text" className="single-car-page__flex__right__contact__msg" />
                <button className="single-car-page__flex__right__btn-yellow">Envoyer le message</button>
              </div>
              }
          </div>


        </div>

         <div className="section__four__title">
            voitures similaires
        </div>
        <ul className="section__four__cars">
          {similarCarsInfo.map((ele, index)=>{
            return <SingleCarCard  key={index} photo={ele.photo[0]} title={ele.name} price={ele.price}/>
          })}
        </ul>
      </div>
      <Footer/>
    </motion.div>
  )
}

export default SingleCarPage