import React from 'react'
import { Link } from 'react-router-dom'
//logo
import footerLogo from "../assets/logo.svg"

const Footer = () => {
  return (
    <footer className="footer">
        {/* <img className="footer__logo" src={footerLogo} />  */}
        <img src="https://digitalfriendly.agency/wp-content/uploads/2021/10/logo-DF.png?fbclid=IwAR07YO152XGm2q8vrztB-8vF4bsAwNOMJzsE_BmdCd8MPWS_zFmjrbMTbyY" style={{width:"8rem"}}/>
        <div className="footer__boxes">
            <div className="footer__box">
                <span>Bureau</span>
                <span>Lorem ipsum dolor sit amet consectetur, adipisicing elit. Ipsam et, saepe libero minima ut distinctio recusandae consequatur reprehenderit labore maxime!</span>
            </div>
            <div className="footer__box">
                <span>Informations </span>
                <Link to="/">Accueil</Link>
                <a style={{cursor:"pointer"}} href="#marque">Marque</a>
                <Link to="/test_drive">Test drive</Link>
                <Link to="/showroom">Showroom</Link>
                <Link to="/contact">contact</Link>
            </div>
            <div className="footer__box">
                <span>Contact</span>
                <span>+216 000 000 00</span>
                <span>Email@Email.com</span>
            </div>
            {/* <div className="footer__box">
                <span style={{fontSize:"1rem"}}>Crer Par</span>
                <span>Digital Friendly </span>
            </div> */}
        </div>
    </footer>
  )
}

export default Footer