import React from 'react'

function CarOptionCard({png, img, content}) {
  return (
    <div className="car-option-card" style={{backgroundRepeat:"no-repeat",backgroundSize:"contain"}}>
        <div className="car-option-card__img" >
                <img style={{width:"8rem"}} src={png} />
        </div>
        <div className="car-option-card__content">
            {content}
        </div>
    </div>
  )
}

export default CarOptionCard